  --foreground: #e2e8f0;
  --card: #0b1120;
  --card-foreground: #e2e8f0;
  --popover: #0b1120;
  --popover-foreground: #e2e8f0;
  --primary: #ff9500;
  --primary-foreground: #000000;
  --secondary: #1e293b;
  --secondary-foreground: #e2e8f0;
  --muted: #1e293b;
  --muted-foreground: #64748b;
  --accent: #ffcc00;
  --accent-foreground: #000000;
  --destructive: #ff3366;
  --destructive-foreground: #000000;
  --border: #ff9500;
  --input: #1e293b;
  --ring: #ff9500;
  --chart-1: #ff9500;
  --chart-2: #ffcc00;
  --chart-3: #ff6600;
  --chart-4: #ffff00;
  --chart-5: #ff3366;
  --radius: 0rem;
  --sidebar: #0b1120;
  --sidebar-foreground: #e2e8f0;
  --sidebar-primary: #ff9500;
  --sidebar-primary-foreground: #000000;
  --sidebar-accent: #1e293b;
  --sidebar-accent-foreground: #e2e8f0;
  --sidebar-border: #ff9500;
  --sidebar-ring: #ff9500;
}
.dark {
  --background: #050814;
  --foreground: #e2e8f0;
  --card: #0b1120;
  --card-foreground: #e2e8f0;
  --popover: #0b1120;
  --popover-foreground: #e2e8f0;
  --primary: #ff9500;
  --primary-foreground: #000000;
  --secondary: #1e293b;
  --secondary-foreground: #e2e8f0;
  --muted: #1e293b;
  --muted-foreground: #64748b;
  --accent: #ffcc00;
  --accent-foreground: #000000;
  --destructive: #ff3366;
  --destructive-foreground: #000000;
  --border: #ff9500;
  --input: #1e293b;
  --ring: #ff9500;
  --chart-1: #ff9500;
  --chart-2: #ffcc00;
  --chart-3: #ff6600;
  --chart-4: #ffff00;
  --chart-5: #ff3366;
  --sidebar: #0b1120;
  --sidebar-foreground: #e2e8f0;
  --sidebar-primary: #ff9500;
  --sidebar-primary-foreground: #000000;
  --sidebar-accent: #1e293b;
  --sidebar-accent-foreground: #e2e8f0;
  --sidebar-border: #ff9500;
  --sidebar-ring: #ff9500;
}
@layer base {
  * {
    outline-color: var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
    }
  }
  body {
    background-color: var(--background);
    font-family: "Geist Mono", "Courier New", monospace;
    color: var(--foreground);
  }
}
@layer utilities {
  .scanlines {
    background: repeating-linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.15),
      rgba(0, 0, 0, 0.15) 1px,
      transparent 1px,
      transparent 2px
    );
  }
  .grid-pattern {
    background-image: linear-gradient(rgba(255, 149, 0, 0.03) 1px, transparent 1px),
      linear-gradient(90deg, rgba(255, 149, 0, 0.03) 1px, transparent 1px);
    background-size: 20px 20px;
  }
  .glow-text {
    text-shadow: 0 0 10px rgba(255, 149, 0, 0.5), 0 0 20px rgba(255, 149, 0, 0.3);
  }
  .glow-border {
    box-shadow: 0 0 10px rgba(255, 149, 0, 0.3), inset 0 0 10px rgba(255, 149, 0, 0.1);
  }
}
